export default{
	name:'testype',
	data() {
		return {
			phoneType: '正在检测...',
			smodel:''
		}
	},
	created() {
		uni.getSystemInfo({
			success: res =>{
				let s = res.model
				let t = s.indexOf('<')
				if(t > 0 && t < 15){
					s = s.substring(0, t)
					console.log('1')
					console.log(t)
					this.phoneType = s
					this.smodel = s
				} else {
					let r = s.indexOf('(')
					if( r > 0 ){
						s = s.substring(0, r)
						console.log(s)
						this.phoneType = s
						this.smodel = s
					} else {
						console.log('3')
						this.phoneType = s
						this.smodel = s
					}
				}
			}
		})
	},
	methods:{
		onNext() {
			uni.navigateTo({
				url: '../../pages/selectmodel/selectmodel'
			})
		},
		bindRepair(){
			uni.navigateTo({
				url:'../../pages/selectmodel/selectmodel?model=' + this.smodel
			})
		}
	}
}
